:root{
    --primary: #FF7A3D;
    --secondary: #FF5100;
  }
  
    h1 {
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      font-size: 50px;
      color: var(--primary);
      margin: 40px 0 ;
    }
        
    .cards__wrapper {
      position: relative;
      margin: 0 200px;
    }
    
    
    .fade-img {
      animation-name: fade-img;
      animation-duration: 2s;
    }
    
    .cards__item__img {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      display: block;
      width: 100%;
      max-width: 100%;
      height: 100%;
      max-height: 100%;
      object-fit: cover;
      transition: all 0.2s linear;
    }
    
    .cards__item__img:hover {
      transform: scale(1.1);
    }
    
    .column {
      float: left;
      width: 33%;
      padding: 0 10px;
    }
    
    .row {
        margin: 0 -5px;
    }

    .hero-btnss{
      margin: -50px 650px 70px 650px;
      position: absolute;

    }
    
    .row:after {
      content: "";
      display: table;
      clear: both;
    }

    .im{
      margin: 2.75rem 10.125rem;
      width: 1100px;
      height: 600px;
    }


    .fa-angle-right:before{
      font-size: 20px;
      color: black;
      margin:  0 15px;
    
    }
    
    @media screen and (max-width: 600px) {
      .column {
        width: 100%;
        display: block;
        margin-bottom: 20px;
      }
    }
       
  

    @media only screen and (max-width: 600px) {
      .im {
        float: none;
        width: 100%;
        margin: auto;
        height: auto;
      }
    }

    
    