:root{
  --primary: #FF7A3D;
  --secondary: #FF5100;
}

.cards {
    padding: 4rem;
    background: #fff;
  }
  
  h1 {
    text-align: center;
    font-size: 50px;
    color: var(--secondary);
    margin-top: 20px;

  }
  
  h4{
    text-align: center;
    color: black; 
    margin-top: 30px;
  }

  p{
    text-align: center;
    margin: 10px 420px;
    width: 42%;   
    font-size: 20px;
    font-family: inherit;
  }

  .abt{
    top: 0 ;
    background-color: #f8f5f4;
  }

  ul >li{
    list-style: none;
  }

  .fa-caret-right{
    margin-right: 10px;
  }

  .element{
    color: rgba(36, 42, 86, 0.06274509803921569);
    font-size: 100px;
    font-weight: bold;
    margin-bottom: 30px;
  }

  .count{
    padding: 5px;
    text-align: left;
    background-color: transparent;
    margin: 10px 20px 10px 50px;   
    width: 80%; 
  }

  .count >h2{
    margin-left: 50px;
    width: 80%;
    color: var(--secondary);
  }
  
  .cards__container {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 1120px;
    width: 90%;
    margin: 0 auto;
  }
  
  .cards__wrapper {
    position: relative;
    margin: 50px 0 45px;
  }
  
  .cards__items {
    margin-bottom: 24px;
  }
  
  .cards__item {
    display: flex;
    flex: 1;
    margin: 0 0.6rem;
    border-radius: 10px;
  }
  
  .cards__item__link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
  }
  
  .cards__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
  }
  
  .fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
  }
  
  .cards__item__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
  }
  
  .cards__item__img:hover {
    transform: scale(1.1);
  }
  
  .cards__item__info {
    padding: 20px 30px ;
  }
  
  .cards__item__text {
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--secondary);
    font-size: 20px;
    line-height: 20px;
  }
  
  .crd{
   display: grid;
   grid-template-columns: repeat(auto-fit, min-max(200px ,1fr));
   gap: 30px;   
  }

  .contact {
    box-shadow: 0px 5px 40px -10px rgb(86 104 146 / 33%);
    margin: 0px 150px;
    border-radius: 6px;
    display: inline-block;
    box-shadow: azure;    
    background-color: #fff;
    color: black;
    padding:  30px;
  }

  .contact >h2{
    color: var(--primary);
  }

  .contact >p{
    display: flex;
    align-items:flex-start;
    color: black;
    margin-left: 0;
    width: 100%;
  }

  * {
    box-sizing: border-box;
  }
  
  body {
    font-family: Arial, Helvetica, sans-serif;
  }
  
  .column {
    float: left;
    width: 33%;
    padding: 0 10px;
  }
  
  .row  {
    margin: 0 -5px;
  }
  
  .row:after {
    content: "";
    display: table;
    clear: both;
  }
  

  
  .cad {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    padding: 16px 0;
    width: 350px;
    height: 500px;
    text-align: center;
    background-color: #fff;
    margin: 30px 0;
  }

    .cad >h3,h2,i{
    color: var(--secondary);
  }

  .cad >p{
    display: block;
    text-align: left;
    width: 300px;
    border: 10px ;
    padding: 0 25px;
    margin: 20px;
    overflow-wrap: break-word;
  }
   
  .drop{    
    text-align: right;
    font-size: 50px;
    color: var(--secondary);
    margin-left: 400px;
    width: 100%;
  }

  #poli{
    background-color: #f8f5f4;
    margin: 20px 160px;
  }

  #poli >p{
    display:flex;
    text-align: left;
    margin: 10px 150px;
    width: 80%;
  }  
  
  @media only screen and (min-width: 1024px) {
    .cards__items {
      display: flex;
    }
  }
  
  @media only screen and (max-width: 1024px) {
    .cards__item {
      margin: auto;
      height: auto;
    }
  }

   
  @media only screen and (max-width: 1024px) {
    .contact  {
      max-width:100%;
      height:auto;
      margin:auto;
    }
  }

  @media only screen and (max-width: 1024px) {
    .res  {      
      max-width: 100%;
      height:auto;
      margin:auto;
    }
  }

  @media screen and (max-width: 1023px) and (min-width: 700px){
    .ress {
      
    }
  }
    
  


 

  