video {
  margin-top: 0;
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
}

.hero-container {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
}

.hero-container > h1 {
  color: #fff;
  margin-top: -100px;
  width: 75%;
  font-size: 60px;
  font-family: 'Montserrat',sans-serif;
  line-height: 1.2;
}

.hero-container > p {
  margin-top: 8px;
  width: 55%;
  color: #fff;
  font-size: 25px;
  font-family: inherit;
}

.hero-btns {
  margin-top: 32px;
}

.hero-btns .btn {
  margin: 6px;
}

.fa-play-circle {
  margin-left: 4px;
}

@media screen and (max-width: 960px) {
  .hero-container > h1 {
    font-size: 70px;
    margin-top: -150px;
  }
}


@media screen and (max-width: 768px) {
    .hero-container > h6 {
    font-size: 15px;
  }
  .hero-container > h1 {
    font-size: 25px;
    margin-top: -100px;
    text-transform: capitalize;
  }

  .hero-container > p {
     overflow-wrap: break-word;
    font-size: 10px;
    font-style: inherit;
    display: block;
 }

 

  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .btn {
    width: 100%;
  }
}